/* eslint-disable jsx-a11y/anchor-is-valid */
import React, {FC} from 'react'
import {KTSVG, toAbsoluteUrl} from '../../../../../_metronic/helpers'
import {Field, ErrorMessage} from 'formik'
import { FormattedMessage, useIntl } from 'react-intl'

const Step4 = (props: any) => {

  const intl = useIntl()

  const handleBonusChange = (e: any) => {
    e.preventDefault();
    const index = e.target.id;

    props.setDiamondTierBonus((s: any) => {
      const newArr = s.slice();
    
      if(e.target.name === 'content') {
        newArr[index] = e.target.value;
      } else {
        newArr[index] = e.target.value;
      }

      return newArr;
    });
  }

  const addBonusInput = () => {
    props.setDiamondTierBonus((s: any) => {
      if(s.length > 0) {
        const lastId = s[s.length - 1].id;
        return [
          ...s,
          ""
        ];
      } else {
        return [
          ...s,
          ""
        ];
      }
    });
  };

  const removeBonusInput = (index: any) => {

    console.log(index)
    props.setDiamondTierBonus((s: any) => {
      if(s.length > 0) {
        const newArr = s.slice();
        newArr.splice(index, 1);
        console.log(newArr)
        return newArr
      } else {
        return s
      }
    });
  };

  console.log(props.diamondTierBonus)

  return (
    <div className='w-100'>
      <div className='mb-10 fv-row'>
        <label className='form-label mb-3 required'><FormattedMessage id='CREATE_EDIT_COLLECTION.TIER' /></label>

        <Field
          type='text'
          className='form-control form-control-lg form-control-solid'
          name=''
          placeholder=''
          value={intl.formatMessage({id: "CREATE_EDIT_COLLECTION.TIER.DIAMOND"})}
          disabled={true}
        />
        <div className='fv-plugins-message-container invalid-feedback'>
          {/* <ErrorMessage name='patentName' /> */}
        </div>
      </div>

      <div className='mb-10 fv-row'>
        <label className='form-label mb-3'>
          <FormattedMessage id='CREATE_EDIT_COLLECTION.TOKEN_IMAGE' />
          <span className='ms-1'>(Recommended size: 500 x 500)</span>
        </label>

        {props.image2 !== "" &&
          <div>
            <img src={URL.createObjectURL(props.image2)} className='w-300px mb-3' />
          </div>
        }

        <div>
          <input type="file" id="image" accept="image/png, image/jpeg" onChange={(e) => props.handleTokenImage2Change(e)} />
        </div>
      </div>

      <div className='mb-10 fv-row'>
        <label className='form-label mb-3 required'>
          <FormattedMessage id='CREATE_EDIT_COLLECTION.OWNERSHIP_OFFERED' /> 
          <span className='ms-1'>(Ownership of a single Diamond tier token in %)</span>
        </label>

        <Field
          type='text'
          className='form-control form-control-lg form-control-solid'
          name='ownershipOffered2'
          placeholder=''
        />
        <div className='fv-plugins-message-container invalid-feedback'>
          {/* <ErrorMessage name='patentName' /> */}
        </div>
      </div>

      <div className='mb-10 fv-row'>
        <label className='form-label mb-3 required'>
          <FormattedMessage id='CREATE_EDIT_COLLECTION.PRICE' /> 
          ({props.selectedBlockchain !== '' && props.selectedBlockchain == 'ethereum' ? 'Price of single diamond tier token in ETH' : 'Price of single diamond tier token in KLAY'})
        </label>

        <Field
          type='text'
          className='form-control form-control-lg form-control-solid'
          name='price2'
          placeholder=''
        />
        <div className='fv-plugins-message-container invalid-feedback'>
          {/* <ErrorMessage name='patentName' /> */}
        </div>
      </div>

      <div className='mb-10 fv-row'>
        <label className='form-label mb-3 required'>
          <FormattedMessage id='CREATE_EDIT_COLLECTION.QUANTITY' />
          <span className='ms-1'>(Total quantity of diamond tier tokens)</span>
        </label>

        <Field
          type='text'
          className='form-control form-control-lg form-control-solid'
          name='quantity2'
          placeholder=''
        />
        <div className='fv-plugins-message-container invalid-feedback'>
          {/* <ErrorMessage name='patentName' /> */}
        </div>
      </div>

      <div className='mb-10 fv-row'>
        <label className='form-label required'>
          <FormattedMessage id='CREATE_EDIT_COLLECTION.SELECT_TOKEN_BENEFITS' />
        </label>

        <div className='mt-3'>
          <label className='form-check form-check-custom form-check-solid align-items-start mt-2 my-2'>
            <input
              className='form-check-input'
              type='checkbox'
              name='email-preferences[]'
              defaultChecked={props.diamondTierBenefitCheckboxData.firstDibsNextDrop}
              onChange={() =>{
                props.handleCheckBoxChange(
                  props.setDiamondTierStdBenefits, 
                  props.setDiamondTierBenefitCheckboxData,
                  `First dibs on the next ${props.artistName ? props.artistName : ''} drop`,
                  props.diamondTierStdBenefits,
                  props.diamondTierBenefitCheckboxData.firstDibsNextDrop,
                  'firstDibsNextDrop'
                )
              }}
            />

            <span className='form-check-label d-flex flex-column align-items-start'>
              <span className='fw-bold fs-5 mb-0'>First dibs on the next {props.artistName ? props.artistName : ''} drop</span>
            </span>
          </label>

          <label className='form-check form-check-custom form-check-solid align-items-start my-2'>
            <input
              className='form-check-input'
              type='checkbox'
              name='email-preferences[]'
              defaultChecked={props.diamondTierBenefitCheckboxData.exclAccessToUnreleased}
              onChange={() => {
                props.handleCheckBoxChange(
                  props.setDiamondTierStdBenefits, 
                  props.setDiamondTierBenefitCheckboxData,
                  'Exclusive access to unreleased music',
                  props.diamondTierStdBenefits,
                  props.diamondTierBenefitCheckboxData.exclAccessToUnreleased,
                  'exclAccessToUnreleased'
                )
              }}
            />

            <span className='form-check-label d-flex flex-column align-items-start'>
              <span className='fw-bold fs-5 mb-0'>
                <FormattedMessage id='CREATE_EDIT_COLLECTION.EXCLUSIVE_ACCESS' />
              </span>
            </span>
          </label>

          <label className='form-check form-check-custom form-check-solid align-items-start my-2'>
            <input
              className='form-check-input'
              type='checkbox'
              name='email-preferences[]'
              defaultChecked={props.diamondTierBenefitCheckboxData.accessToRoyalCollectorChannel}
              onChange={() => {
                props.handleCheckBoxChange(
                  props.setDiamondTierStdBenefits, 
                  props.setDiamondTierBenefitCheckboxData,
                  'Access to Royal collector Discord channel',
                  props.diamondTierStdBenefits,
                  props.diamondTierBenefitCheckboxData.accessToRoyalCollectorChannel,
                  'accessToRoyalCollectorChannel'
                )
              }
              }
            />

            <span className='form-check-label d-flex flex-column align-items-start'>
              <span className='fw-bold fs-5 mb-0'>
                <FormattedMessage id='CREATE_EDIT_COLLECTION.ACCESS_TO_DISCORD' />
              </span>
            </span>
          </label>

          <label className='form-check form-check-custom form-check-solid align-items-start my-2'>
            <input
              className='form-check-input'
              type='checkbox'
              name='email-preferences[]'
              defaultChecked={props.diamondTierBenefitCheckboxData.exclusiveContent}
              onChange={() => {
                props.handleCheckBoxChange(
                  props.setDiamondTierStdBenefits, 
                  props.setDiamondTierBenefitCheckboxData,
                  'Exclusive Content',
                  props.diamondTierStdBenefits,
                  props.diamondTierBenefitCheckboxData.exclusiveContent,
                  'exclusiveContent'
                )
              }
              }
            />

            <span className='form-check-label d-flex flex-column align-items-start'>
              <span className='fw-bold fs-5 mb-0'>
                <FormattedMessage id='CREATE_EDIT_COLLECTION.EXCLUSIVE_CONTENT' />
              </span>
            </span>
          </label>

          <label className='form-check form-check-custom form-check-solid align-items-start my-2 mb-2'>
            <input
              className='form-check-input'
              type='checkbox'
              name='email-preferences[]'
              defaultChecked={props.diamondTierBenefitCheckboxData.firstDibAllDrop}
              onChange={() => {
                props.handleCheckBoxChange(
                  props.setDiamondTierStdBenefits, 
                  props.setDiamondTierBenefitCheckboxData,
                  `First dibs on all future${props.artistName ? props.artistName : ''} drops`,
                  props.diamondTierStdBenefits,
                  props.diamondTierBenefitCheckboxData.firstDibAllDrop,
                  'firstDibAllDrop'
                )
              }
              }
            />

            <span className='form-check-label d-flex flex-column align-items-start'>
              <span className='fw-bold fs-5 mb-0'>First dibs on all future {props.artistName ? props.artistName : ''} drops</span>
            </span>
          </label>
        </div>

        <div className='fv-plugins-message-container invalid-feedback'>
          {/* <ErrorMessage name='patentAbstract' /> */}
        </div>
      </div>

      <div className='mb-10 fv-row'>
        <label className='form-label required'><FormattedMessage id='CREATE_EDIT_COLLECTION.ADD_CUSTOM_BENEFITS' /></label>
        {props.diamondTierBonus.map((bonus: any, index: any) => (
          <>
          <textarea
            key={bonus.id}
            name=''
            id={index}
            className='form-control form-control-lg form-control-solid mt-4'
            rows={5}
            value={bonus}
            onChange={(event) => handleBonusChange(event)}
          ></textarea>

          {/* <div className='fv-plugins-message-container invalid-feedback'>
            <ErrorMessage name='collectionDescription' />
          </div> */}
          <button key={index} onClick={() => removeBonusInput(index)} type='button' className='btn btn-danger mt-4'>
            <FormattedMessage id='CREATE_EDIT_COLLECTION.REMOVE_CUSTOM_BENEFITS' />
          </button>
          </>
        ))}
      </div>
      <button onClick={addBonusInput} type='button' style={{ background: '#ffd600', color: '#000', borderColor: '#0000' }} className='py-2 px-4 rounded'>
        <FormattedMessage id='CREATE_EDIT_COLLECTION.ADD_NEW_BENEFITS' />
      </button>
    </div>
  )
}

export {Step4}
