import React, {FC, useEffect, useRef, useState} from 'react'
import {useSelector, shallowEqual, useDispatch} from 'react-redux'
import {Step1} from './steps/Step1'
import {Step2} from './steps/Step2'
import {Step3} from './steps/Step3'
import {KTSVG} from '../../../../_metronic/helpers'
import {StepperComponent, IStepperOptions} from '../../../../_metronic/assets/ts/components'
import {Formik, Form, FormikValues} from 'formik'
import {
  createAccountSchemas, 
  ICreateAccount, 
  inits, 
  tokenBenefitdefaultChecboxData0,
  tokenBenefitdefaultChecboxData1,
  tokenBenefitdefaultChecboxData2,
  ITokenBenefitCheckboxData
} from './CreateAccountWizardHelper'
import {createCollection} from '../../explore/redux/ecomCrud'
import {RootState} from '../../../../setup'
import {AlertDanger, AlertSuccess} from '../../alerts/Alerts'
import * as auth from '../../auth/redux/AuthRedux'
import {actions} from '../../explore/redux/ecomRedux'
import { Step4 } from './steps/Step4'
import { FormattedMessage } from 'react-intl'


const defaultStepperOptions: IStepperOptions = {
  startIndex: 1,
  animation: false,
  animationSpeed: '0.3s',
  animationNextClass: 'animate__animated animate__slideInRight animate__fast',
  animationPreviousClass: 'animate__animated animate__slideInLeft animate__fast',
}

const Horizontal: FC = () => {
  const stepperRef = useRef<HTMLDivElement | null>(null)
  const stepper = useRef<StepperComponent | null>(null)
  const [currentSchema, setCurrentSchema] = useState(createAccountSchemas[0])
  const [initValues] = useState<ICreateAccount>(inits)

  const {walletAddress} : any = useSelector<RootState>(
    ({auth}) => ({
      walletAddress: auth.walletAddress
    })
  )

  const getCurrentDataTime = () => {
    let now = new Date();
    let utcString = now.toISOString().substring(0,19);
    let year = now.getFullYear();
    let month = now.getMonth() + 1;
    let day = now.getDate();
    let hour = now.getHours();
    let minute = now.getMinutes();
    let second = now.getSeconds();
    var localDatetime = year + "-" +
                      (month < 10 ? "0" + month.toString() : month) + "-" +
                      (day < 10 ? "0" + day.toString() : day) + "T" +
                      (hour < 10 ? "0" + hour.toString() : hour) + ":" +
                      (minute < 10 ? "0" + minute.toString() : minute) +
                      utcString.substring(16,19);

    return localDatetime
  }

  const [loading, setLoading] = useState<boolean>(false)
  const [checked, setChecked] = useState<boolean>(false)
  const [image, setImage] = useState<any>("")
  const [dropCoverImage, setDropCoverImage] = useState<any>("")
  const [tokenImage0, setTokenImage0] = useState<any>("")
  const [tokenImage1, setTokenImage1] = useState<any>("")
  const [tokenImage2, setTokenImage2] = useState<any>("")
  const [legalPDF, setLegalPDF] = useState<any>("")
  const [successMessage, setSuccessMessage] = useState<string>("")
  const [errorMessage, setErrorMessage] = useState<any>("")
  const dispatch = useDispatch()

  const [artistName, setArtistName] = useState<string>('')
  const [selectedArtist, setSelectedArtist] = useState<string>('')

  const [goldTierBenefitCheckboxData, setGoldTierBenefitCheckboxData] = useState<ITokenBenefitCheckboxData>(tokenBenefitdefaultChecboxData0)
  const [platinumTierBenefitCheckboxData, setPlatinumTierBenefitCheckboxData] = useState<ITokenBenefitCheckboxData>(tokenBenefitdefaultChecboxData1)
  const [diamondTierBenefitCheckboxData, setDiamondTierBenefitCheckboxData] = useState<ITokenBenefitCheckboxData>(tokenBenefitdefaultChecboxData2)

  const [selectedGoldTier, setSelectedGoldTier] = useState<boolean>(false)
  const [selectedPlatinumTier, setSelectedPlatinumTier] = useState<boolean>(false)
  const [selectedDiamondTier, setSelectedDiamondTier] = useState<boolean>(false)
  const [selectedBlockchain, setSelectedBlockchain] = useState<string>('')

  const [goldTierStdBenefits, setGoldTierStdBenefits] = useState<Array<any>>([])
  const [platinumTierStdBenefits, setPlatinumTierStdBenefits] = useState<Array<string>>([])
  const [diamondTierStdBenefits, setDiamondTierStdBenefits] = useState<Array<string>>([])

  const [goldTierBonus, setGoldTierBonus] = useState<Array<any>>([])
  const [platinumTierBonus, setPlatinumTierBonus] = useState<Array<string>>([])
  const [diamondTierBonus, setDiamondTierBonus] = useState<Array<string>>([])

  const [currentIndex, setCurrentIndex] = useState<any>(null)
  const [dropReleaseDateTime, setdropReleaseDateTime] = useState(getCurrentDataTime())
  const [selectedAnyTiers, setSelectedAnyTiers] = useState<boolean>(selectedGoldTier || selectedPlatinumTier || selectedDiamondTier)

  useEffect(() => {
    if (!stepperRef.current) {
      return
    }

    loadStepper()
  }, [stepperRef])

  useEffect(() => {
    if (!stepperRef.current) {
      return
    }

    let step = new StepperComponent(stepperRef.current as HTMLDivElement, defaultStepperOptions)
    stepper.current = step

    setSelectedAnyTiers(selectedGoldTier || selectedPlatinumTier || selectedDiamondTier)
  }, [selectedGoldTier, selectedPlatinumTier, selectedDiamondTier])

  useEffect(() => {
    if(stepper.current?.currentStepIndex && stepper.current.totatStepsNumber) {
      setCurrentIndex(stepper.current?.currentStepIndex)
    }
  }, [stepper.current])
  
  if(successMessage !== "") {
    setTimeout(() => {
      setChecked(false);
      setTimeout(() => {
        setSuccessMessage("");
      }, 200);
    }, 5000);
  }

  if(errorMessage !== "") {
    setTimeout(() => {
      setChecked(false);
      setTimeout(() => {
        setErrorMessage("");
      }, 200);
    }, 5000);
  }

  const handleImageChange = (e: any) => {
    setImage(e.target.files[0])
  };

  const handleDropCoverImageChange = (e: any) => {
    setDropCoverImage(e.target.files[0])
  };

  const handleTokenImage0Change = (e: any) => {
    setTokenImage0(e.target.files[0])
  };

  const handleTokenImage1Change = (e: any) => {
    setTokenImage1(e.target.files[0])
  };

  const handleTokenImage2Change = (e: any) => {
    setTokenImage2(e.target.files[0])
  };

  const handlePDFChange = (e: any) => {
    setLegalPDF(e.target.files[0])
  }

  const {authToken, userID, userName}: any = useSelector<RootState>(
    ({auth}) => ({
      authToken: auth.accessToken,
      userID: auth.user?.userID,
      userName: auth.user?.username
    }), shallowEqual
  )

  const loadStepper = () => {
    stepper.current = StepperComponent.createInsance(stepperRef.current as HTMLDivElement)
    console.log(stepper.current)
    console.log("stepper loaded")
  }

  const prevStep = () => {
    if (!stepper.current) {
      return
    }

    stepper.current.goPrev()
    setCurrentIndex((index: any) => {return index - 1})

    // setCurrentSchema(createAccountSchemas[stepper.current.currentStepIndex - 1])
  }

  const toggleBenefitCheckboxData = (tierBenefitCheckboxDataSetter: any ,property: any) => {
    switch (property) {
      case 'firstDibsNextDrop':
        tierBenefitCheckboxDataSetter((data: any) => {
          let newData = data
          newData.firstDibsNextDrop = !data.firstDibsNextDrop
          return newData
        })
        break;
      case 'firstDibAllDrop':
        tierBenefitCheckboxDataSetter((data: any) => {
          let newData = data
          newData.firstDibAllDrop = !data.firstDibAllDrop
          return newData
        })
        break;
      case 'accessToRoyalCollectorChannel':
        tierBenefitCheckboxDataSetter((data: any) => {
          let newData = data
          newData.accessToRoyalCollectorChannel = !data.accessToRoyalCollectorChannel
          return newData
        })
        break;
      case 'exclusiveContent':
        tierBenefitCheckboxDataSetter((data: any) => {
          let newData = data
          newData.exclusiveContent = !data.exclusiveContent
          return newData
        })
        break;
      case 'exclAccessToUnreleased':
        tierBenefitCheckboxDataSetter((data: any) => {
          let newData = data
          newData.exclAccessToUnreleased = !data.exclAccessToUnreleased
          return newData
        })
        break;
    }
  }

  const checkRequiredFields = (
    collectionName: string, 
    collectionOwner: string,
    collectionType: string, 
    spotifyURL: string, 
    collectionDescription: string,
    goldTierPrice: any,
    platinumTierPrice: any,
    diamondTierPrice: any,
    ownershipOffered0: any,
    ownershipOffered1: any,
    ownershipOffered2: any,
    quantity0: any,
    quantity1: any,
    quantity2: any,
  ) => {
    if(artistName === '') {
      return {valStatus: false, msg: 'Please select artist name'}
    }
    if(image === "") {
      return {valStatus: false, msg: 'Drop image is missing'}
    }
    if(dropCoverImage === "") {
      return {valStatus: false, msg: 'Drop cover image is missing'}
    }
    if(legalPDF === "") {
      return {valStatus: false, msg: 'Legal Document is missing'}
    }
    if(collectionName === '') {
      return {valStatus: false, msg: 'Please enter collection name'}
    }
    if(collectionOwner === '') {
      return {valStatus: false, msg: 'Please enter collection owner'}
    }
    if(collectionType === '') {
      return {valStatus: false, msg: 'Please select collection type'}
    }
    if(selectedBlockchain === '') {
      return {valStatus: false, msg: 'Please select Blockchain'}
    }
    if(collectionDescription === '') {
      return {valStatus: false, msg: 'Please enter collection description'}
    }
    if(selectedGoldTier) {
      if(tokenImage0 === '') {
        return {valStatus: false, msg: 'Gold tier token image is missing'}
      } 
      if(goldTierPrice === '' || ownershipOffered0 === '' || quantity0 === '') {
        if(parseFloat(goldTierPrice) < 0 || parseFloat(ownershipOffered0) < 0 || parseInt(quantity0) < 0) {
          return {valStatus: false, msg: 'Entered invalid gold tier data'}
        }
      }
    }
    if(selectedPlatinumTier) {
      if(tokenImage1 === '') {
        return {valStatus: false, msg: 'Platinum tier token image is missing'}
      } 
      if(platinumTierPrice === '' || ownershipOffered1 === '' || quantity1 === '') {
        if(parseFloat(platinumTierPrice) < 0 || parseFloat(ownershipOffered1) < 0 || parseInt(quantity1) < 0) {
          return {valStatus: false, msg: 'Entered invalid platinum tier data'}
        }
      }
    }
    if(selectedDiamondTier) {
      if(tokenImage2 === '') {
        return {valStatus: false, msg: 'Diamond tier token image is missing'}
      } 
      if(diamondTierPrice === '' || ownershipOffered2 === '' || quantity2 === '') {
        if(parseFloat(diamondTierPrice) < 0 || parseFloat(ownershipOffered2) < 0 || parseInt(quantity2) < 0) {
          return {valStatus: false, msg: 'Entered invalid platinum tier data'}
        }
      }
    }
    return {valStatus: true, msg: null}
  }

  const getCombinedObject = (stdBenefits: Array<any>, custBenefits: Array<any>) => {
    return {
      "stdBenefits" : stdBenefits,
      "custBenefits": custBenefits
    }
  }

  const handleCheckBoxChange = (
    stateSetter: any, 
    tierBenefitCheckboxDataSetter: any,
    text: any, 
    state: Array<any>, 
    isRemovingElement: boolean, 
    property: any
  ) => {

    toggleBenefitCheckboxData(tierBenefitCheckboxDataSetter, property)

    if(!isRemovingElement) {
      stateSetter((state: any) => {
        return [...state, text]
      })
    } else {
        let newState = state.filter((data) => data !== text)
        stateSetter(newState);
    }
  }

  const resetForm = () => {
    setImage("")
    setDropCoverImage("")
    setLegalPDF("")
    setTokenImage0("")
    setTokenImage1("")
    setTokenImage2("")
    setArtistName("")
    setGoldTierBenefitCheckboxData(tokenBenefitdefaultChecboxData0)
    setPlatinumTierBenefitCheckboxData(tokenBenefitdefaultChecboxData1)
    setDiamondTierBenefitCheckboxData(tokenBenefitdefaultChecboxData2)
    setSelectedGoldTier(false)
    setSelectedPlatinumTier(false)
    setSelectedDiamondTier(false)
    setSelectedBlockchain("")
    setGoldTierStdBenefits([])
    setPlatinumTierStdBenefits([])
    setDiamondTierStdBenefits([])
    setGoldTierBonus([])
    setPlatinumTierBonus([])
    setDiamondTierBonus([])
  }

  const submitStep = (values: ICreateAccount, actions: FormikValues) => {
    if (!stepper.current) {
      return
    }
    console.log("step changed")

    // setCurrentSchema(createAccountSchemas[stepper.current.currentStepIndex])

    if(!(selectedGoldTier || selectedPlatinumTier || selectedDiamondTier)) {
      return
    }

    if (stepper.current.currentStepIndex !== stepper.current.totatStepsNumber) {
      // if(stepper.current.currentStepIndex == 1 ) {
      //   if(
      //     values.collectionName === '') {
      //     window.scrollTo(0,0)
      //     setErrorMessage("Please enter a collection name")
      //     setChecked(true)
      //     setLoading(false)
      //     return
      //   }

      //   if(values.collectionType === '') {
      //     window.scrollTo(0,0)
      //     setErrorMessage("Please select a collection type")
      //     setChecked(true)
      //     setLoading(false)
      //     return
      //   }

      //   if(values.collectionDescription === '') {
      //     window.scrollTo(0,0)
      //     setErrorMessage("Please enter a collection description")
      //     setChecked(true)
      //     setLoading(false)
      //     return
      //   }

      //   if(values.collectionOwner === '') {
      //     window.scrollTo(0,0)
      //     setErrorMessage("Please enter a collection owner")
      //     setChecked(true)
      //     setLoading(false)
      //     return
      //   }
      // }
      stepper.current.goNext()
      setCurrentIndex((index: any) => {return index + 1})
    } else {
      setLoading(true)
      const valData = checkRequiredFields(
        values.collectionName,
        values.collectionOwner,
        values.collectionType,
        values.spotifyURL,
        values.collectionDescription,
        values.price0,
        values.price1,
        values.price2,
        values.ownershipOffered0,
        values.ownershipOffered1,
        values.ownershipOffered2,
        values.quantity0,
        values.quantity1,
        values.quantity2
      )
      if(valData.valStatus) {      
        const formData = new FormData()
        formData.append('authToken', authToken)
        formData.append('image', image)
        formData.append('dropCoverImage', dropCoverImage)
        formData.append('legalPDF', legalPDF)
        formData.append('artistID', selectedArtist)
        formData.append('collectionName', values.collectionName)
        formData.append('collectionOwner', values.collectionOwner)
        formData.append('spotifyURL', values.spotifyURL)
        formData.append('dropReleaseDateTime', dropReleaseDateTime)
        formData.append('collectionType', values.collectionType)
        formData.append('blockchain', selectedBlockchain)

        let colDescriptArr = values.collectionDescription.split('\n')
        formData.append('collectionDescription', JSON.stringify(colDescriptArr))
        formData.append('hasGoldTier', selectedGoldTier.toString())
        formData.append('hasPlatinumTier', selectedPlatinumTier.toString())
        formData.append('hasDiamondTier', selectedDiamondTier.toString())
        if(selectedGoldTier) {
          formData.append('goldTierImage', tokenImage0)
          formData.append('ownershipOfferedForGoldTier', values.ownershipOffered0)
          formData.append('goldTierPrice', values.price0)
          formData.append('goldTierQuantity', values.quantity0)
          formData.append('goldTierBenefits', JSON.stringify(getCombinedObject(goldTierStdBenefits, goldTierBonus)))
        }
        if(selectedPlatinumTier) {
          formData.append('platinumTierImage', tokenImage1)
          formData.append('ownershipOfferedForPlatinumTier', values.ownershipOffered1)
          formData.append('platinumTierPrice', values.price1)
          formData.append('platinumTierQuantity', values.quantity1)
          formData.append('platinumTierBenefits', JSON.stringify(getCombinedObject(platinumTierStdBenefits, platinumTierBonus)))
        }
        if(selectedDiamondTier) {
          formData.append('diamondTierImage', tokenImage2)
          formData.append('ownershipOfferedForDiamondTier', values.ownershipOffered2)
          formData.append('diamondTierPrice', values.price2)
          formData.append('diamondTierQuantity', values.quantity2)
          formData.append('diamondTierBenefits', JSON.stringify(getCombinedObject(diamondTierStdBenefits, diamondTierBonus)))
        }

        createCollection(
          formData
        ).then(
          (response) => {
            if(response.data.success) {
              setSuccessMessage(response.data.message)
              setChecked(true)
              setLoading(false)
              stepper.current?.goto(1)
              window.scrollTo(0,0)
              setCurrentIndex(1)
              setImage("")
              // setEvents(eventArray)
              actions.resetForm()
              resetForm()
            } else {
              if(response.data.message) {
                setErrorMessage(response.data.message)
              } else {
                setErrorMessage(<FormattedMessage id="CREATE_EDIT_COLLECTION.COLLECTION_CREATION_FAILED" />)
              }

              setChecked(true)
              setLoading(false)
              stepper.current?.goto(1)
              window.scrollTo(0,0)
              setCurrentIndex(1)
            }
          }
        ).catch((error) => {
          localStorage.setItem('sessionTimeOut', 'Session timed out')
          dispatch(auth.actions.logout())
        })
      } else {
        window.scrollTo(0,0)
        setErrorMessage(valData.msg)
        setChecked(true)
        setLoading(false)
      }
    }
  }

  return (
    <>
    {successMessage !== "" ? (
          <AlertSuccess message={successMessage} checked={checked} />
    ) : null }

    {errorMessage !== "" ? (
      <AlertDanger message={errorMessage} checked={checked} />
      ) : null }

    <div className='card'>
      <div className='card-body'>
        <div
          ref={stepperRef}
          className='stepper stepper-links d-flex flex-column pt-15'
          id='kt_create_account_stepper'
        >
          <div className='stepper-nav mb-5'>
            <div className='stepper-item current' data-kt-stepper-element='nav'>
              <h3 className='stepper-title'><FormattedMessage id="CREATE_EDIT_COLLECTION.COLLECTION_INFO" /></h3>
            </div>

            {selectedGoldTier && 
              <div className='stepper-item' data-kt-stepper-element='nav'>
                <h3 className='stepper-title'><FormattedMessage id="CREATE_EDIT_COLLECTION.GOLD_TIER_INFO" /></h3>
              </div>
            }

            {selectedPlatinumTier && 
              <div className='stepper-item' data-kt-stepper-element='nav'>
                <h3 className='stepper-title'><FormattedMessage id="CREATE_EDIT_COLLECTION.PLATINUM_TIER_INFO" /></h3>
              </div>
            }

            {selectedDiamondTier && 
              <div className='stepper-item' data-kt-stepper-element='nav'>
                <h3 className='stepper-title'><FormattedMessage id="CREATE_EDIT_COLLECTION.DIAMOND_TIER_INFO" /></h3>
              </div>
            }

            {/* <div className='stepper-item' data-kt-stepper-element='nav'>
              <h3 className='stepper-title'>Prosecution History</h3>
            </div> */}
          </div>

          <Formik validationSchema={currentSchema} initialValues={initValues} onSubmit={submitStep}>
            {() => (
              <Form
                className='mx-auto mw-600px w-100 pt-15 pb-10'
                noValidate
                id='kt_create_account_form'
              >
                <div className='current' data-kt-stepper-element='content'>
                  <Step1 
                    handleImageChange={handleImageChange} 
                    image={image}
                    handleDropCoverImageChange={handleDropCoverImageChange}
                    handlePDFChange={handlePDFChange}
                    legalPDF={legalPDF}
                    dropCoverImage={dropCoverImage}
                    selectedArtist={selectedArtist}
                    setSelectedArtist={setSelectedArtist}
                    artistName={artistName}
                    setArtistName={setArtistName}
                    selectedGoldTier={selectedGoldTier}
                    selectedPlatinumTier={selectedPlatinumTier}
                    selectedDiamondTier={selectedDiamondTier}
                    setSelectedGoldTier={setSelectedGoldTier}
                    setSelectedPlatinumTier={setSelectedPlatinumTier}
                    setSelectedDiamondTier={setSelectedDiamondTier}
                    selectedBlockchain={selectedBlockchain}
                    setSelectedBlockchain={setSelectedBlockchain}
                    dropReleaseDateTime={dropReleaseDateTime}
                    setdropReleaseDateTime={setdropReleaseDateTime}
                  />
                </div>

                {selectedGoldTier &&
                  <div data-kt-stepper-element='content'>
                    <Step2 
                      image0={tokenImage0}
                      handleTokenImage0Change={handleTokenImage0Change}
                      artistName={artistName}
                      selectedBlockchain={selectedBlockchain}
                      goldTierBonus={goldTierBonus}
                      setGoldTierBonus={setGoldTierBonus}
                      setGoldTierBenefitCheckboxData={setGoldTierBenefitCheckboxData}
                      goldTierBenefitCheckboxData={goldTierBenefitCheckboxData}
                      setGoldTierStdBenefits={setGoldTierStdBenefits}
                      goldTierStdBenefits={goldTierStdBenefits}
                      handleCheckBoxChange={handleCheckBoxChange}
                    />
                  </div>
                }

                {selectedPlatinumTier &&
                  <div data-kt-stepper-element='content'>
                    <Step3 
                      image1={tokenImage1}
                      handleTokenImage1Change={handleTokenImage1Change}
                      artistName={artistName}
                      selectedBlockchain={selectedBlockchain}
                      platinumTierBonus={platinumTierBonus}
                      setPlatinumTierBonus={setPlatinumTierBonus}
                      setPlatinumTierBenefitCheckboxData={setPlatinumTierBenefitCheckboxData}
                      platinumTierBenefitCheckboxData={platinumTierBenefitCheckboxData}
                      setPlatinumTierStdBenefits={setPlatinumTierStdBenefits}
                      platinumTierStdBenefits={platinumTierStdBenefits}
                      handleCheckBoxChange={handleCheckBoxChange}
                    />
                  </div>
                }

                {selectedDiamondTier &&
                  <div data-kt-stepper-element='content'>
                    <Step4 
                      image2={tokenImage2}
                      artistName={artistName}
                      handleTokenImage2Change={handleTokenImage2Change}
                      selectedBlockchain={selectedBlockchain}
                      diamondTierBonus={diamondTierBonus}
                      setDiamondTierBonus={setDiamondTierBonus}
                      setDiamondTierBenefitCheckboxData={setDiamondTierBenefitCheckboxData}
                      diamondTierBenefitCheckboxData={diamondTierBenefitCheckboxData}
                      setDiamondTierStdBenefits={setDiamondTierStdBenefits}
                      diamondTierStdBenefits={diamondTierStdBenefits}
                      handleCheckBoxChange={handleCheckBoxChange}
                    />
                  </div>
                }

                <div className='d-flex flex-stack pt-15'>
                  <div className='mr-2'>
                    <button
                      onClick={prevStep}
                      type='button'
                      className='btn btn-lg btn-light-primary me-3'
                      data-kt-stepper-action='previous'
                    >
                      <KTSVG
                        path='/media/icons/duotone/Navigation/Left-2.svg'
                        className='svg-icon-4 me-1'
                      />
                      <FormattedMessage id="CREATE_EDIT_COLLECTION.BACK" />
                    </button>
                  </div>

                  <div>
                    {loading && (
                          <button 
                            style={{ background: '#ffd600', color: '#000', borderColor: '#0000' }} 
                            type='submit' 
                            className='py-2 px-4 me-3 rounded'
                          >
                            <FormattedMessage id="CREATE_EDIT_COLLECTION.SUBMITTING" />..
                            <span className='spinner-border spinner-border-sm align-middle ms-2'></span>
                          </button>
                      )
                    }

                    {!loading && (
                        <button 
                          type='submit' 
                          style={{ background: '#ffd600', color: '#000', borderColor: '#0000' }} 
                          className='py-2 px-4 me-3 rounded' 
                          disabled={!selectedAnyTiers}
                        >
                        <span className='indicator-label'>
                          {currentIndex !== undefined &&
                            currentIndex !==
                            stepper.current?.totatStepsNumber! && <FormattedMessage id="CREATE_EDIT_COLLECTION.CONTINUE" />}
                          {
                            currentIndex === undefined && <FormattedMessage id="CREATE_EDIT_COLLECTION.CONTINUE" />}
                            
                          {currentIndex !== undefined &&
                            currentIndex ==
                            stepper.current?.totatStepsNumber! && <FormattedMessage id="CREATE_EDIT_COLLECTION.SUBMIT" />}
                          <KTSVG
                            path='/media/icons/duotone/Navigation/Right-2.svg'
                            className='svg-icon-3 ms-2 me-0'
                          />
                        </span>
                      </button>
                      )}
                  </div>
                </div>
              </Form>
            )}
          </Formik>
        </div>
      </div>
    </div>
    </>
  )
}

export {Horizontal}
